.social-list {
    margin-top: 30px;
    display: flex;
    align-items: baseline;
    justify-content: center;
  }
  
  .social-item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  
  .social-item-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    background-color: #fff;
    border-radius: 50%;
    outline: none;
  
  
    &:hover .social-icon, &:focus .social-icon {
      fill: $accent-color;
      
    }
  }
  
  .social-icon {
    width: 20px;
    height: 20px;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    fill: #303030;
  }